/* #Pagina adicionar contato CSS */

.adicionar-container{
  background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left bottom, left top, from(#0F2027), color-stop(#0F2027), to(#0F2027));
  background: linear-gradient(to top, #0F2027, #0F2027, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 93vh;
}

