}


.signupSection {
  /* background: url(https://source.unsplash.com/TV2gg2kZD1o/1600x800); */
  background-repeat: no-repeat;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 800px;
  height: 450px;
  text-align: center;
  display: flex;
  color: white;
  box-shadow: 3px 10px 20px 5px rgba(253, 253, 253, 0.7);
  font-family: 'Open Sans', sans-serif;
}

.info {
  width: 45%;
  background: rgba(20, 20, 20, .8);
  padding: 30px 0;
  border-right: 5px solid rgba(30, 30, 30, .8);
  font-family: 'Open Sans', sans-serif;
}
.sign-title {
    padding-top: 30px;
    font-weight: 300;
    font-family: 'Open Sans', sans-serif;
  }
.adicionar-texto {
    font-size: 18px;
  }
.icon {
    font-size: 8em;
    padding: 20px 0;
    color: rgba(10, 180, 180, 1);
  }

.signupForm {
  width: 70%;
  padding: 30px 0;
  /*   */
  background: #0f0c29;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left bottom, left top, from(#302b63), to(#000046));
  background: linear-gradient(to top, #302b63, #000046); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
  -webkit-transition: .2s;  
  transition: .2s;
}
.sign-title {
    font-weight: 300;
    font-family: 'Open Sans', sans-serif;
  }

.inputFields {
  margin: 15px 0;
  font-size: 16px;
  padding: 10px;
  width: 250px;
  border: 1px solid rgb(10, 70, 180);
  border-top: none;
  border-left: none;
  border-right: none;
  background: rgba(20, 20, 20, .2);
  color: white;
  outline: none;
}
.inputFields:focus {
  border: 1px solid rgb(10, 180, 19);
  border-top: none;
  border-left: none;
  border-right: none;
  background: rgba(20, 20, 20, .1);
}
.noBullet {
  list-style-type: none;
  padding: 0;
}

#join-btn {
  border: 1px solid rgb(10, 146, 180);
  background: rgba(20, 20, 20, .6);
  font-size: 18px;
  color: white;
  margin-top: 20px;
  padding: 10px 50px;
  cursor: pointer;
  -webkit-transition: .4s;
  transition: .4s;
}

#join-btn:hover {
    background: rgba(20, 20, 20, .8);
    border: 1px solid rgba(10, 180, 180, 1);
    padding: 10px 80px;
  }



